	// animate 
	@mixin animate($animation, $duration, $easing, $repeat) {
		-webkit-animation: $animation $duration $easing $repeat;
		-moz-animation: $animation $duration $easing $repeat;
		-ms-animation: $animation $duration $easing $repeat;
		-o-animation: $animation $duration $easing $repeat;
		animation: $animation $duration $easing $repeat;
	}

	// keyframes 
	@mixin keyframes($name) {
		@-webkit-keyframes #{$name} {
			@content;
		}

		@-moz-keyframes #{$name} {
			@content;
		}

		@-ms-keyframes #{$name} {
			@content;
		}

		@keyframes #{$name} {
			@content;
		}
	}

	// rtl 
	@mixin rtl {
		[dir="rtl"] & {
			@content;
		}
	}